header {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 80%;
    justify-content: center;
    justify-items: center;
    align-items: center;
}

header h1 {
    font-family: 'ProtestRiot';
    font-size: 40px;
    color: var(--nametext-clr);
}

nav {
    display: flex;
    flex-direction: row;
    gap: 40px;
    padding: 20px;
    align-items: center;
    border: 1px solid var(--navborder-clr);
    border-radius: 40px;
}

nav button {
    color: var(--navtext-clr);
    font-size: 16px;
    text-transform: uppercase;
}

nav button:hover {
    cursor: pointer;
    text-decoration: underline;
}

.selected {
    text-decoration: underline;
}
